.paint-right {
    transform: scale(-1, 1);
    margin-top: 15%;
    float: right;
    width: 400%;
    z-index: -1;
}

.paint-left {
    margin-top: 15%;
    width: 400%;
    z-index: -1;
}

.card-body, .card-header {
    padding: 0.7rem 1.25rem;
    border-bottom: 1px solid rgba(0,0,0,.125);
}

@media only screen
and (max-device-width : 768px){ .paint-left, .paint-right { display: none; }}
